<style lang="less" scoped>
  @import "./SelectBox.less";
</style>

<template>
  <div class="comp-x-label-select">
    <ul class="comp-label-selected hide">
      <template v-if="list.selected && list.selected.length > 0">
        <li v-for="li in list.selected">
          <span>{{li}}</span>
          <span class="glyphicon glyphicon-remove close" @click="handlerDelLi(li)"></span>
        </li>
      </template>
      <template v-else>
        <div class="emptyTip">请设置属性</div>
      </template>

    </ul>
    <div class="tips hide" v-if="list.maxInput != 0">元素最多选择{{list.maxInput}}个</div>
    <div class="comp-label-list">
      <div class="err-msg"></div>
      <ul class="list" :class="{'list-disable': disable }">
        <li v-for="val in list.values" :class="{ selected: temp[val] }" @click="handlerAddLi(val)">{{val}}</li>
      </ul>
      <div class="add-item" v-if="list.isCustomizable === '1'">
        <input type="text" class="form-control input-add-item" v-model.trim="customer" placeholder="自定义"
               @keyup.enter="handleAddItem(customer)"><button class="btn btn-info" @click="handleAddItem(customer)">+</button>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    props: [
      'result',
      'index'
    ],
    data: function () {
      return {
        list: this.result,
        temp: {},
        disable: false,
        customer: ''
      }
    },

    created: function () {

      // this.list.selected = this.list.selected ? this.list.selected : [];

    },

    mounted: function () {
      this.init()
    },

    methods: {
      init: function () {
        this.formatData();
      },
      formatData: function () {
        let values = this.list.values || [];
        let selected = this.list.selected || [];
        let tmp = {};

        for (let val of values) {
          tmp[val] = 0;
        }

        for (let val of selected) {
          tmp[val] = 1;
        }

        // 判断长度
        this.disable = selected.length >= Math.max(this.list.maxInput, 2);

        this.temp = tmp;

      },
      _del: function (arr, val) {
        let index = arr.indexOf(val);
        let list = this.list;
        this.temp[val] = 0;
        arr.splice(index, 1);
        this.list = Object.assign({}, list, {selected: arr});
        // 判断长度
        this.disable = arr.length >= this.list.maxInput;
      },
      _add: function (arr, val) {
        let list = this.list;

        // let len = this.list.maxInput == 0 ? this.list.values.length : this.list.maxInput;
        if (+this.list.maxInput === 1) {
          // 单选
          arr = [];
          this.temp = {};
          this.temp[val] = 1;
          arr.push(val);
          this.list = Object.assign({}, list, {selected: arr});
        } else {
          // 多选
          if (+this.list.maxInput !== 0 && arr.length >= this.list.maxInput) {
            // alert('元素最多选择' + this.list.maxInput + '个');
            return false;
          }
          this.temp[val] = 1;
          arr.push(val);
          this.list = Object.assign({}, list, {selected: arr});
          // 判断长度
          this.disable = arr.length >= this.list.maxInput;
        }

      },
      handleAddItem: function (val) {
        if (this.list.values && this.list.values.indexOf(val) > -1) {
          this.customer = '';
          return false;
        }
        this.list.values = this.list.values || [];
        this.list.selected = this.list.selected || [];
        this.list.values.push(val);
        if (this.list.selected.length < this.list.maxInput) {
            this.list.selected.push(val);
        }
        this.customer = '';
        this.handlerAddLi(val);
      },
      handlerAddLi: function (val) {
        let arr = Object.assign([], this.list.selected);
        if (!arr) {
          arr = [];
        }
        if (this.temp[val]) {
          this._del(arr, val);
        } else {
          this._add(arr, val);
        }

      },
      handlerDelLi: function (val) {

        let arr = this.list.selected;
        this._del(arr, val);

        let list = this.list;
        this.list = Object.assign({}, list, {selected: arr});
      },

    },
    watch: {
      'list': function () {
        this.$emit('resultChange', this.list, this.index);
      },
      'result': {
        handler: function (_new, _old) {
          this.list = _new;
          this.init();
        },
        deep: true
      },

    }
  }
</script>



