import Vue from 'vue'
import $ from 'jquery'

import ImageUpload from './components/common/ImageUpload.vue'
import SelectBox from './components/common/SelectBox.vue'
import MultipleSelect from './components/common/MultipleSelect.vue'
import preview from './components/common/preview.vue'
import Toast from './components/common/toast'

import APIS from './constants/apis/api'
import VueResource from 'vue-resource'

Vue.use(VueResource);

import VueDND from 'awe-dnd'
Vue.use(VueDND);

/**
 * 数组去重
 * @param arr
 * @return {Array}
 */
const uniqueArray = (arr) => {
  let tmpArr = [];
  for (let i = 0; i < arr.length; i += 1) {
    if (tmpArr.indexOf(arr[i]) === -1) {
      tmpArr.push(arr[i]);
    }
  }
  return tmpArr;
  // es6 set 模式
  // return [...new Set(arr)];
};

export default {
  components: {
    ImageUpload,
    preview,
    SelectBox,
    MultipleSelect,
  },
  data () {
    return {
      isPre: (this.getQueryStringByName('pre') === '1') || false,
      // lockId:'',
      showImgList: [],
      isShow: false,
      isLoading: false,
      originData: {},

      sizeName: '',
      styleName: '',
      descFieldName: '',
      tempSizeDelta: 0,

      // 上新之后页面初始化拉去小店原价
      isSkuModify: false,

      articleInfo: {
        color: '',
        size: '',
      },

      goodsInfo: {
        itemTitle: '', // 标题
        simpleDescription: '', // 简述
        itemNo: '', // 编码
        category: '',
        frontCategory: [],
        mgjFrontCategory: [],
        classId: '',
        coverImgs: [], // 封面图
        _coverImgs: [], // 封面图
        staticDomain: '',
        onNew: '',
        cpsRate: '',
        mlsProvider: false,
        addSizeInfoImageFlag: '',
        addRecommendSizeImageFlag: ''
      },

      cpsRateList: [],

      skuInfo: {
        color: [],
        size: [],
        imgs: [],
        discount: '',
        discountId: '',
        dcId: '',
        discountList: [],
        discountStr: '',
        count: '',
        skuNo: '',
        list: [],
        rate: '',
        rateList: [],
        // stockRate:'',
        // stockRateDiy:'',
        // stockRateList:[]
      },
      goodsDes: {
        list: [],
        sizedDes: '',
        des: '',
        detailImgs: [],
        _detailImgs: [],
        details: '',
        sizeConfig: []
      },

      propertyList: [],

      buyerExperience: [],

      buyerOnTry: {
        modelConfig: [],
        onTryData: {
          modelId: 0,
          size: '',
          description: '',
          imgs: [],
          _imgs: [],
          sizeEffect: []
        },
        effectConfig: [],
      },

      category: [],
      frontCategory: [],
      mgjFrontCategory: [],
      frontCategorySubList: {},
      mgjFrontCategoryPlatform: {},
      mgjFrontCategorySubList: {},
      cid: ''

    }
  },
  mounted: function () {
    this.init();

    // 关闭事件
    if (!this.isPre) {
      $(window).bind('beforeunload', function () {
        return '您输入的内容尚未保存，确定离开此页面吗？';
      });
    }

  },
  created: function () {

  },

  methods: {
    init(){
      console.log('init!!!');
      this.getInfo();
    },
    getInfo: function (obj) {
      let id = this.getQueryStringByName('id');
      let pre = this.getQueryStringByName('pre') || 0;
      let param = obj || {};
      let cid = this.cid || this.getQueryStringByName('cid') || '';

      let url = '';

      if (param.isCid) {
        url = APIS.GET_INFO + '?id=' + id + '&pre=' + pre;
      } else {
        url = APIS.GET_INFO + '?id=' + id + '&cid=' + cid + '&pre=' + pre;
      }

      let self = this;
      let toast = Toast.loading({
        message: '加载中…',
        duration: 0
      });
      this.$http.get(url,{
        headers:{
          "Cache-Control": "no-cache, max-age=0, must-revalidate, no-store"
        }
      })
        .then((response) => {
          let data;
          if ('object' === typeof response.data) {
            data = response.data;
          } else {
            data = JSON.parse(response.data);
          }
          if (data.code === 0) {
            self.originData = data.data;
            self.formatData();
            toast.remove();
          } else {
            Toast.error({
              message: data.msg,
              duration: 0
            });
          }
        })
        .catch(function (response) {
          Toast.error({
            message: '加载数据出错，请重试！',
            duration: 0
          });
          console.log(response);
        })
    },
    formatData: function () {
      let _this = this;
      let d = this.originData;
      let _goodsInfo = d.goodsInfo;

      // this.lockId = d.lockId;
      this.sizeName = d.sizeName || '尺码';
      this.styleName = d.styleName || '颜色';
      this.descFieldName = d.descFieldName || '穿着效果';
      this.goodsInfo = {
        id: _goodsInfo.id,
        itemTitle: _goodsInfo.itemTitle,
        simpleDescription: _goodsInfo.simpleDescription,
        itemNo: _goodsInfo.itemNo,
        category: _goodsInfo.category,
        frontCategory: _goodsInfo.frontCategory,
        mgjFrontCategory: _goodsInfo.mgjFrontCategory,
        classId: _goodsInfo.classId,
        coverImgs: _goodsInfo.coverImgs.map(function (item) {
          if (item && item.indexOf('http') > -1) {
            return item
          }
          return _goodsInfo.staticDomain + item;
        }),
        staticDomain: _goodsInfo.staticDomain,
        bdPrice: _goodsInfo.bdPrice,
        onNew: _goodsInfo.onNew,
        cpsRate: _goodsInfo.cpsRate,
        mlsProvider: _goodsInfo.mlsProvider,
        // 是否是套装
        isSuit: +_goodsInfo.isSuit === 1,
        // 是否需要尺码
        addSizeInfoImageFlag: _goodsInfo.addSizeInfoImageFlag || 1,
        addRecommendSizeImageFlag: _goodsInfo.addRecommendSizeImageFlag || 1,
        // 供货商材质参考
        providerMaterial: _goodsInfo.providerMaterial || ''
      };
      this.cpsRateList = d.cpsRateList;
      this.frontCategory = d.frontCategory;
      this.mgjFrontCategory = d.mgjFrontCategory;
      // frontCategorySubList
      for (let item of d.frontCategory) {
        this.frontCategorySubList[item.id] = item.subList;
      }
      for (let item of d.mgjFrontCategory) {
        this.mgjFrontCategoryPlatform[item.id] = item.subList;
        for (let subItem of item.subList) {
          this.mgjFrontCategorySubList[subItem.id] = subItem.subList;
        }
      }

      // 删除不存在的一级、二级类目
      let tempFrontCategory = this.goodsInfo.frontCategory;
      tempFrontCategory.forEach((item, index) => {
        // 一级分类
        if (!this.frontCategorySubList[item.id]) {
          this.goodsInfo.frontCategory.splice(index, 1);
        } else {
          // 二级分类如果被删除，则设置二级ID为空
          let noSubItem = true;
          for (let subItem of this.frontCategorySubList[item.id]) {
            if (item.subId == subItem.id) {
              noSubItem = false;
            }
          }
          if (noSubItem) {
            item.subId = '';
          }
        }
      });

      tempFrontCategory = this.goodsInfo.mgjFrontCategory;
      tempFrontCategory.forEach((item, index) => {
        if (!this.mgjFrontCategoryPlatform[item.platform]) {
          // 平台被删除
          this.goodsInfo.mgjFrontCategory.splice(index, 1);
        } else if (!this.mgjFrontCategorySubList[item.id]) {
          // 一级分类如果被删除，则设置一级、二级ID为空
          item.id = '';
          item.subId = '';
        } else {
          // 三级分类如果被删除，则设置三级ID为空
          let noSubItem = true;
          for (let subItem of this.mgjFrontCategorySubList[item.id]) {
            if (item.subId == subItem.id) {
              noSubItem = false;
            }
          }
          if (noSubItem) {
            item.subId = '';
          }
        }
      });

      // 为空处理
      if (!this.goodsInfo.frontCategory.length) {
        this.goodsInfo.frontCategory.push({id:'', subId:''});
      }
      if (!this.goodsInfo.mgjFrontCategory.length) {
        this.goodsInfo.mgjFrontCategory.push({id:'', subId:'', platform:''});
      }

      // 初始化对等
      this.goodsInfo._coverImgs = this.goodsInfo.coverImgs;

      this.skuInfo = {
        color: d.skuColors,
        size: d.skuSizes,
        imgs: d.skuImgs.map((item) => {
          if (!item.image) {
            delete item.image
          }

          return item
        }),
        // imgs: d.skuImgs.map((item) => item.image),
        discountList: d.discountList,
        discount: _goodsInfo.discount,
        discountId: _goodsInfo.discountId,
        // stockRate:_goodsInfo.stockRate,
        // stockRateList:d.stockRateList,
        price: '',
        count: '',
        skuNo: '',
        list: d.skuInfo,
        rate: _goodsInfo.rate,
        rateList: d.rateList,
        fixedPriceMass: '',
      };

      // for (let i = 0; i < d.stockRateList.length; i += 1) {
      //   if (_goodsInfo.stockRate === d.stockRateList[i].rate) {
      //     this.skuInfo.stockRateDiy = '';
      //     break;
      //   } else {
      //     this.skuInfo.stockRateDiy = _goodsInfo.stockRate;
      //   }
      // }

      let _sizeList = _goodsInfo.sizeList,
        _sizeConfig = d.sizeConfig,
        _size = d.skuSizes;

      if (_sizeConfig.length < 1) {
        _sizeList = [];
      } else {
        // 过滤无效尺码
        _sizeList = _sizeList.filter(function (item) {
          return _size.indexOf(item[0].value) > -1;
        });

        // 有尺码信息，但没尺码说明的，需要补全
        let tempSizeListObj = {};
        // 提取已有sizeList中的尺码key
        for (let _sizeListItem of _sizeList) {
          tempSizeListObj[_sizeListItem[0].value] = 1;
        }
        // 检测尺码信息中无尺码说明的尺码，并创建尺码说明
        for (let _sizeItem of _size) {
          if (!tempSizeListObj[_sizeItem]) {
            let emptySizeList = [];
            for (let _sizeConfigItem of _sizeConfig) {
              let obj = {
                key: _sizeConfigItem === this.sizeName ? this.sizeName : _sizeConfigItem,
                value: ''
              };
              emptySizeList.push(obj);
            }
            // 修改空尺码说明的第一列表头为尺码信息中的值
            emptySizeList[0].value = _sizeItem;
            _sizeList.push(emptySizeList);
          }
        }

      }

      this.goodsDes = {
        list: _sizeList,
        sizedDes: _goodsInfo.sizeDescription,
        details: _goodsInfo.details,
        detailImgs: _goodsInfo.detailImgs.map(function (item) {
          if (item && item.indexOf('http') > -1) {
            return item;
          }
          return _goodsInfo.staticDomain + item;
        }),
        sizeConfig: d.sizeConfig
      };
      // 初始化对等
      this.goodsDes._detailImgs = this.goodsDes.detailImgs;

      this.propertyList = d.propertyList;

      this.propertyList.map(function (item) {
        item.selected = item.selected ? item.selected : [];
        item.values = uniqueArray((item.values || []).concat(item.selected));
        return item;
      });

      // 买手亲测
      this.buyerExperience = d.buyerExperience;

      // 买手试穿

      let _buyerOnTry = d.buyerOnTry;

      if (!_buyerOnTry.onTryData.modelId) {
        _buyerOnTry.onTryData = {
          modelId: '',
          size: '',
          description: '',
          imgs: [],
          _imgs: [],
          sizeEffect: []
        }
      }

      this._formatBuyerData(_buyerOnTry);
    },
    handlerPlatformChange: function (index) {
      this.goodsInfo.mgjFrontCategory[index].id = '';
      this.goodsInfo.mgjFrontCategory[index].subId = '';
    },
    // 一级类目变更以后，二级类目重置
    handlerFrontCategoryChange: function (index,type) {
      if (0 === type) {
        this.goodsInfo.frontCategory[index].subId = '';
      } else {
        this.goodsInfo.mgjFrontCategory[index].subId = '';
      }
    },
    handlerSizeAutoFill: function (index, delta) {
      if (this.goodsDes.list[0][index]['value'] === '') {
        return ;
      }
      for (let i = 1, len = this.goodsDes.list.length; i < len; i++) {
        this.goodsDes.list[i][index]['value'] = parseFloat(this.goodsDes.list[i - 1][index]['value']) + parseFloat(delta);
      }
    },
    _formatBuyerData: function (obj) {
      if (obj.onTryData.sizeEffect && obj.onTryData.sizeEffect.length > 0) {
        obj.modelConfig.forEach(function (item) {
          for (let _item of obj.onTryData.sizeEffect) {
            if (_item.modelId == item.id) {
              item.isCur = true
            }
          }
          return item;
        });
      }
      this.buyerOnTry = obj;

    },
    restoreData: function () {

      let self = this;
      let goodsInfoData = this.goodsInfo, skuInfoData = this.skuInfo, goodsDesData = this.goodsDes;
      let goodsInfo = Object.assign(self.originData.goodsInfo, {
          itemTitle: goodsInfoData.itemTitle,
          simpleDescription: goodsInfoData.simpleDescription,
          itemNo: goodsInfoData.itemNo,
          // 组件上传待优化
          coverImgs: goodsInfoData._coverImgs || goodsInfoData.coverImgs,
          detailImgs: goodsDesData._detailImgs || goodsInfoData.detailImgs,
          discount: skuInfoData.discount,
          // stockRate:skuInfoData.stockRate || skuInfoData.stockRateDiy.replace(/：/g, ':'),
          sizeList: goodsDesData.list,
          details: goodsDesData.details,
          sizeDescription: goodsDesData.sizedDes,
          discountId: skuInfoData.discountId,
          rate: skuInfoData.rate,
          cpsRate: goodsInfoData.cpsRate,
          frontCategory: goodsInfoData.frontCategory,
          mgjFrontCategory: goodsInfoData.mgjFrontCategory,
          addSizeInfoImageFlag: goodsInfoData.addSizeInfoImageFlag,
          addRecommendSizeImageFlag: goodsInfoData.addRecommendSizeImageFlag,
        }),
        skuInfo = skuInfoData.list,
        skuImgs = skuInfoData.imgs,
        skuColors = skuInfoData.color,
        skuSizes = skuInfoData.size;

      // 打补丁，以后优化
      this.buyerOnTry.onTryData.imgs = this.buyerOnTry.onTryData._imgs || this.buyerOnTry.onTryData.imgs;

      // 为什么要删除？
      // delete goodsInfoData._coverImgs;
      // delete goodsDesData._detailImgs;
      delete this.buyerOnTry.onTryData._imgs;

      let data = Object.assign({}, this.originData, {goodsInfo: goodsInfo}, {skuInfo: skuInfo}, {skuImgs: skuImgs}, {skuColors: skuColors}, {skuSizes: skuSizes}, {propertyList: this.propertyList}, {buyerExperience: this.buyerExperience}, {buyerOnTry: this.buyerOnTry});

      return data;

    },
    handleGiveup() {
      if (confirm("要离开此网站吗，系统可能不会保存您做的更改？")) {
        let id = this.getQueryStringByName('id')

        let url = APIS.CANCEL_EDIT + '?id=' + id

        this.$http.get(url)
          .then((response) => {
            let data;
            if ('object' === typeof response.data) {
              data = response.data;
            } else {
              data = JSON.parse(response.data);
            }
            if (data.code === 0) {
              Toast.success({
                message: '放弃编辑成功',
                duration: 500
              });
              setTimeout(function () {
                $(window).off('beforeunload');
                window.location.href = "http://selection.meili-inc.com/"
              }, 500);
            } else {
              Toast.error({
                message: '放弃编辑失败，请重试！',
                duration: 0
              });
              console.log(response);
            }
          })
          .catch(function (response) {
            Toast.error({
              message: '放弃编辑失败，请重试！',
              duration: 0
            });
            console.log(response);
          })
      }

    },
    handleSubmit() {
      let self = this;
      // 检测佣金比例
      if (+this.skuInfo.rate === 0) {
        Toast.error({
          message: '请选择佣金比例！',
          duration: 1000
        });
        return false;
      }
      // 前台分类校验
      if (this.goodsInfo.frontCategory.length === 1 && !this.goodsInfo.frontCategory[0].id && !this.goodsInfo.frontCategory[0].subId) {
        // 未设置分类
        this.goodsInfo.frontCategory = [];
      } else {
        // 设置分类
        let tempObj = {};
        for (let item of this.goodsInfo.frontCategory) {
          let tempKey = '' + item.id + item.subId;
          // 检测是否重复
          if (tempObj[tempKey]) {
            Toast.error({
              message: '前台分类(美丽说)有重复，请检查！',
              duration: 1000
            });
            return false;
          }
          tempObj[tempKey] = 1;

          // 检测是否填写一级分类，如果只有一条数据，并且没选一级分类，则可提交
          if (!item.id) {
            Toast.error({
              message: '请删除未填写的前台分类(美丽说)！',
              duration: 1000
            });
            return false;
          }

          // 检测是否填写二级分类
          if (this.frontCategorySubList[item.id] && this.frontCategorySubList[item.id].length && !item.subId) {
            Toast.error({
              message: '请选择前台分类(美丽说)的二级分类！',
              duration: 1000
            });
            return false;
          }
        }
      }
      // 前台分类校验
      if (this.goodsInfo.mgjFrontCategory.length === 1 && !this.goodsInfo.mgjFrontCategory[0].platform && !this.goodsInfo.mgjFrontCategory[0].id && !this.goodsInfo.mgjFrontCategory[0].subId) {
        // 未设置分类
        this.goodsInfo.mgjFrontCategory = [];
      } else {
        // 设置分类
        let tempObj = {};
        for (let item of this.goodsInfo.mgjFrontCategory) {
          let tempKey = '' + item.platform + item.id + item.subId;
          // 检测是否重复
          if (tempObj[tempKey]) {
            Toast.error({
              message: '前台分类(蘑菇街)有重复，请检查！',
              duration: 1000
            });
            return false;
          }
          tempObj[tempKey] = 1;

          // 检测是否填写一、二级分类，如果只有一条数据，并且没选一级分类，则可提交
          if (!item.platform || (item.platform && !item.id)) {
            Toast.error({
              message: '前台分类(蘑菇街)填写有误！',
              duration: 1000
            });
            return false;
          }

          // 检测是否填写三级分类
          if (this.mgjFrontCategorySubList[item.id] && this.mgjFrontCategorySubList[item.id].length && !item.subId) {
            Toast.error({
              message: '前台分类(蘑菇街)填写有误！',
              duration: 1000
            });
            return false;
          }
        }
      }

      // 删除尺码后检查尺码效果
      for (let i = 0, item, len = this.buyerOnTry.onTryData.sizeEffect.length; i < len; i++) {
        item = this.buyerOnTry.onTryData.sizeEffect[i];
        if (this.skuInfo.size.indexOf(item.trySize) === -1) {
          Toast.error({
            message: '请检查尺码效果里的试穿尺码！',
            duration: 1000
          });
          return false;
        }
      }

      // 检查材质属性是否选择了"其它"
      let showMaterialTip = false;
      for (let item of this.propertyList) {
        if ('材质' === item.propertyName && item.selected.indexOf('其他') > -1) {
          showMaterialTip = true;
          break;
        }
      }
      if (showMaterialTip && !window.confirm('该商品的材质为【其他】，请确认是否保存？')) {
        return false;
      }

      let data = this.restoreData();

      // if (eval(data.goodsInfo.stockRate.replace(/:/g, '+')) !== 10) {
      //   Toast.error({
      //     message: '自定义库存比例错误',
      //     duration: 1000
      //   });
      //   return false;
      // }

      // this.isLoading = true;
      Toast.loading({
        message: '保存中，请稍后…',
        duration: 0
      });
      let isOnNew = arguments[0] == 1;
      if (isOnNew) {
        Object.assign(data, {
          onNew: 1
        });
      }

      this.isShow = false;
      this.$http.post(APIS.SAVE_INFO, data)
        .then((response) => {
          console.log('********** SAVE_INFO success**********');
          console.log(response.data);
          let data;
          if ('object' === typeof response.data) {
            data = response.data;
          } else {
            data = JSON.parse(response.data);
          }
          // this.isLoading = false;
          // alert(data.msg);
          if (data.code === 0) {
            Toast.success({
              message: '保存成功',
              duration: 500
            });
            // 为了让用户看到保存成功状态，延时操作
            setTimeout(function () {
              $(window).off('beforeunload');
              if (data.itemUrlId) {
                window.location.href = 'http://item.meilishuo.com/detail/' + data.itemUrlId;
              } else {
                window.location.reload();
              }

              // if (isOnNew) {

              //   if (data.itemUrlId) {

              //   } else {
              //     window.location.href = "/on-new?onNew=0"
              //   }
              // } else {
              //   $(window).off('beforeunload');
              //   window.location.href = "http://selection.meili-inc.com/";
              //   // self.getInfo({isCid: true});
              // }
            }, 500);
          } else {
            Toast.error({
              message: data.msg,
              duration: 3000
            });
          }
        })
        .catch(function (response) {
          console.log('********** SAVE_INFO fail**********');
          console.log(response);
          Toast.error({
            message: '保存失败，请重试！',
            duration: 2000
          });
          // this.isLoading = false;
          // alert('链接错误！！！');
        });

      // let errObj = this.$vuerify.$errors;
      /*if (this.$vuerify.check()) { // 手动触发校验所有数据
       // do something
       }else{
       for( var k in errObj ){
       }
       }*/
    },
    handlerSKUImageChange: function (obj, index) {
      var temp = this.skuInfo.imgs;
      if (obj.length > 0) {
        temp[index].image = obj.join('');
        // temp[index].warn = obj.join('');
      } else {
        delete temp[index].image
      }

      this.skuInfo.imgs = [];
      this.skuInfo.imgs = temp;

    },
    handlerCoverImageChange: function (obj) {
      this.goodsInfo._coverImgs = obj;
      this.isShow = false;
    },
    handlerDetailImageChange: function (obj) {
      this.goodsDes._detailImgs = obj;
      this.isShow = false;
    },
    handlerPreview: function (obj) {
      this.showImgList = obj;
      this.isShow = true;
    },
    handlerPreviewAll: function () {
      let self = this;
      this.showImgList = this.goodsDes._detailImgs || this.goodsDes.detailImgs;
      this.showImgList = this.showImgList.map(function (item) {
        if (item && item.indexOf('http') > -1) {
          return item
        }
        return self.goodsInfo.staticDomain + item;
      });
      this.isShow = true;
    },
    handlerDelColor: function (n, color) {
      this.skuInfo.color.splice(n, 1);
      this.skuInfo.imgs.splice(n, 1);

      this.skuInfo.list = this.skuInfo.list.filter((item, index, array) => {
        return item.color != color
      });

    },
    handlerAddType: function (type) {

      let val = this.articleInfo[type].trim();
      if (!val) {
        // alert('添加不能为空');
        return
      }
      if (this.skuInfo[type].indexOf(val.toUpperCase()) > -1 || this.skuInfo[type].indexOf(val.toLowerCase()) > -1) {
        // alert('不能添加重复！');
        return
      }

      this.skuInfo[type].push(val);
      this.articleInfo[type] = '';

      // 更新数据
      if (type === 'color') {
        this.skuInfo.imgs.push({
          color: val
        });
        this.updateSkuListByColor(val);
      }

      if (type === 'size') {
        this.updateSkuListBySize(val);
        this.updateSizeList(val);
      }
    },
    // 更新sku list
    updateSkuListByColor: function (val) {

      let _salePrice = '';

      if (this.skuInfo.list.length > 0 && this.skuInfo.list[0].salePrice) {
        _salePrice = this.skuInfo.list[0].salePrice
      } else {
        _salePrice = this.goodsInfo.bdPrice
      }

      let obj = {
        id: "",
        color: val,
        size: "",
        price: "",
        salePrice: _salePrice,
        bdPrice: this.goodsInfo.bdPrice,
        amount: "",
        skuNo: "",
        image: "",
        isNew: true
      }, arr = [];

      // let obj = Object.assign( {}, this.skuInfo.list[0], {color: val, id: ''} ), arr = [];

      this.skuInfo.size.forEach(function (item) {
        // 数组复制，更新
        let _obj = Object.assign({}, obj, {size: item});
        arr.push(_obj);

      });

      this.skuInfo.list = this.skuInfo.list.concat(arr);

    },
    updateSkuListBySize: function (val) {

      let _salePrice = '';

      if (this.skuInfo.list.length > 0 && this.skuInfo.list[0].salePrice) {
        _salePrice = this.skuInfo.list[0].salePrice
      } else {
        _salePrice = this.goodsInfo.bdPrice
      }

      let obj = {
        id: "",
        color: '',
        size: val,
        price: "",
        salePrice: _salePrice,
        bdPrice: this.goodsInfo.bdPrice,
        amount: "",
        skuNo: "",
        image: "",
        isNew: true
      }, arr = [];

      // let obj = Object.assign( {}, this.skuInfo.list[0], {size: val, id: ''} ), arr = [];

      this.skuInfo.color.forEach(function (item) {
        // 数组复制，更新

        let _obj = Object.assign({}, obj, {color: item});
        arr.push(_obj);
      });
      this.skuInfo.list = this.skuInfo.list.concat(arr);
    },
    updateSizeList: function (val) {
      let obj;
      if (this.goodsDes.list.length > 0) {

        obj = $.extend(true, [], this.goodsDes.list[0]);

        obj[0] = {
          key: this.sizeName,
          value: val
        };
        this.goodsDes.list.push(obj);
      } else {
        obj = [];
        this.goodsDes.sizeConfig.forEach((item, index) => {
          obj.push({
            key: item,
            value: ''
          });
        });
        obj[0].value = val;
        this.goodsDes.list = [obj]
      }
    },
    handlerDelSize: function (n, size) {
      this.skuInfo.size.splice(n, 1);
      this.skuInfo.list = this.skuInfo.list.filter((item, index, array) => {
        return item.size != size
      });
      this.goodsDes.list = this.goodsDes.list.filter((item, index, array) => {
        return item[0].value != size
      });
    },
    handlerInput: function (event, count) {
      let $el = $(event.target),
        $count = $el.siblings('.inputCount');

      let _count = $el.val().length;

      if (_count > count) {
        return false
      }

    },
    handlerSku: function () {

      let _skuInfo = this.skuInfo;

      this.skuInfo.list.map((item) => {

        if (item.isNew) {
          item.salePrice = _skuInfo.price || item.salePrice;
          item.amount = _skuInfo.count || item.amount;
          item.skuNo = _skuInfo.skuNo || item.skuNo;
        }
        if (_skuInfo.fixedPriceMass !== '') {
          item.fixedPrice = _skuInfo.fixedPriceMass || '';
        }
      });

      this.skuInfo.price = '';
      this.skuInfo.count = '';
      this.skuInfo.skuNo = '';
      this.skuInfo.fixedPriceMass = '';

    },
    handlerDragDetailImgs: function (obj) {
      this.goodsDes.detailImgs = obj;
    },
    downloadAll: function () {
      let imgs = this.goodsDes._detailImgs || this.goodsDes.detailImgs;
      let _url = '?url=' + JSON.stringify(imgs);
      let url = APIS.DOWNLOAD_IMG + _url;
      window.open(url, '_blank');
    },
    downloadAllCoverImg: function () {
      let $el = this.$el;

      let imgs = this.goodsInfo._coverImgs || this.goodsInfo.coverImgs;
      let _url = '?url=' + JSON.stringify(imgs);
      let url = APIS.DOWNLOAD_IMG + _url;
      window.open(url, '_blank');

    },
    getQueryStringByName: function (name) {
      let result = location.search.match(new RegExp("[\?\&]" + name + "=([^\&]+)", "i"));
      if (result == null || result.length < 1) {
        return "";
      }
      return result[1];
    },
    changePreview: function () {
      this.isShow = false
    },
    // propertyList
    handlerPropertyListChange: function (obj, index) {
      this.propertyList[index] = obj;
    },
    // 更新商品分类
    updateCategory: function (obj, index) {

      this.getInfo();

    },

    // 加载 Category
    loadCategory: function () {
      let self = this;
      this.$http.get(APIS.CATEGORY)
        .then((response) => {
          let data;
          if ('object' === typeof response.data) {
            data = response.data;
          } else {
            data = JSON.parse(response.data);
          }
          self.category = data.data;
        })
        .catch(function (response) {
          console.log(response)
        })
    },
    // 添加 美丽说分类
    addFrontCategory: function (type) {
      if (0 === type) {
        this.goodsInfo.frontCategory.push({
          id: '',
          subId: ''
        });
      } else {
        this.goodsInfo.mgjFrontCategory.push({
          id: '',
          subId: '',
          platform: ''
        });
      }

    },
    // 删除 美丽说分类
    deleteFrontCategory: function (idx,type) {
      if (0 === type) {
        this.goodsInfo.frontCategory.splice(idx, 1);
      } else {
        this.goodsInfo.mgjFrontCategory.splice(idx, 1);
      }
    },
    // 获取多级联动的 categoryId
    handlerCategoryChange: function (id) {

      this.cid = id;

    },
    // 买手亲测
    handlerBuyerDel: function (index) {
      this.buyerExperience.splice(index, 1);
    },
    handlerBuyerAdd: function () {
      let obj = {
        title: "",
        description: "",
        id: 0
      };
      this.buyerExperience.push(obj);
    },
    handlerBuyerTryImageChange: function (obj) {
      this.buyerOnTry.onTryData._imgs = obj;
    },
    handlerBuyerSize: function (index, event) {
      let n = this.buyerOnTry.modelConfig[index].isCur || false;
      let _obj = this.buyerOnTry.modelConfig[index];
      _obj.isCur = !n;
      this.$set(this.buyerOnTry.modelConfig, index, _obj);
      let sizeEffect = this.buyerOnTry.onTryData.sizeEffect || [];
      if (_obj.isCur) {
        let d = {
          modelId: _obj.id,
          nickName: _obj.nickName,
          heightWeight: _obj.modelHigh + '/' + _obj.modelWeight,
          bwh: _obj.modelBwh,
          commonSize: '',
          trySize: '',
          effect: '',
          sizeList: _obj.sizeList
        };
        sizeEffect.push(d);
      } else {
        let id = _obj.id;
        sizeEffect = sizeEffect && sizeEffect.filter(function (item, index) {
            return item.modelId != id;
          });

      }
      this.$set(this.buyerOnTry.onTryData, 'sizeEffect', sizeEffect);
    },
    handlerPreviewAllBuyer: function () {
      let self = this;
      this.showImgList = this.buyerOnTry.onTryData._imgs || this.buyerOnTry.onTryData.imgs;
      this.isShow = true;
    },
    downloadAllBuyer: function () {

      let _imgs = this.buyerOnTry.onTryData._imgs || this.buyerOnTry.onTryData.imgs;
      let _url = '?url=' + JSON.stringify(_imgs)
      let url = APIS.DOWNLOAD_IMG + _url;
      window.open(url, '_blank');
    },
    handlerFirstImageChange: function (obj, index) {
      this.buyerOnTry.onTryData.firstImg = obj[0];

    },
    handlerBuyerExperienceImageChange: function (obj, index) {
      let temp = this.buyerExperience;
      /*console.log( temp );
       console.log( obj );
       console.log( index );*/

      if (obj.length > 0) {
        temp[index].image = obj.join('');
        // temp[index].warn = obj.join('');
      } else {
        delete temp[index].image
      }

      this.buyerExperience = [];
      this.buyerExperience = temp;

    },
  },
  watch: {
    'skuInfo.discountId': function (_newValue, _oldValue) {
      let self = this;

      this.skuInfo.discountList.forEach(function (item, index) {
        if (_newValue == item.id) {
          self.skuInfo.discount = item.discount;
        }
      });
    },
    'skuInfo.imgs': function (_newValue, _oldValue) {

    }
  }
}



