<style lang="less" scoped>
  @import "./MultipleSelect.less";
</style>

<template>
  <div class="multipleSelect">
    <h2>清选择分类：</h2>
    <div class="row">
      <div class="col-xs-2 col-sm-2">
        <select class="form-control" v-model="first" @change="select1To2">
          <option value="">请选择</option>
          <option v-for="(cate, index) in result" :value="cate.categoryId">{{cate.name}}</option>
        </select>
      </div>

      <div class="col-xs-2 col-sm-2">
        <select class="form-control" v-model="second" v-if="secondObj.isLeaf == 0" @change="select2To3">
          <option value="">请选择</option>
          <option v-for="(cate, index) in secondObj.children" :value="cate.categoryId">{{cate.name}}</option>
        </select>
      </div>

      <div class="col-xs-2 col-sm-2">
        <select class="form-control" v-model="third" v-if="thirdObj.isLeaf == 0" @change="select3To4">
          <option value="">请选择</option>
          <option v-for="(cate, index) in thirdObj.children" :value="cate.categoryId">{{cate.name}}</option>
        </select>
      </div>

      <div class="col-xs-2 col-sm-2">
        <select class="form-control" v-model="fourth" v-if="fourthObj.isLeaf == 0" @change="select4To5">
          <option value="">请选择</option>
          <option v-for="(cate, index) in fourthObj.children" :value="cate.categoryId">{{cate.name}}</option>
        </select>
      </div>

      <div class="col-xs-2 col-sm-2">
        <select class="form-control" v-model="fifth" v-if="fifthObj.isLeaf == 0"  @change="select5To6">
          <option value="">请选择</option>
          <option v-for="(cate, index) in fifthObj.children" :value="cate.categoryId">{{cate.name}}</option>
        </select>
      </div>
    </div>
  </div>


</template>

<script>
  /**
   *  参数说明
   */
  export default {
    props: [
      'result',
      'index'
    ],
    data: function(){
      return {
        category: this.result,
        temp: {},
        first: '',
        second: '',
        secondObj: {},
        third: '',
        thirdObj: {},
        fourth: '',
        fourthObj: '',
        fifth: '',
        fifthObj: {},
        sixth: '',
        sixthObj: {},
        categoryId: ''
      }
    },
    created: function(){

      // this.list.selected = this.list.selected ? this.list.selected : [];

    },
    mounted: function(){
      this.init();
      this.select1To2()
    },
    updated: function(){

    },
    methods: {
      init: function(){

      },
      handlerCategoryId: function( obj ){
        if( obj.isLeaf == 1 ){
          this.categoryId = obj.categoryId
        }
      },
      select1To2: function(){

        for( let obj of this.result ){
          if( obj.categoryId == this.first ){
            this.secondObj = obj;
          }
        }

        this.handlerCategoryId( this.secondObj );

        console.log( '********* second *********' );
        console.log( this.secondObj );

        // 清楚后面select值
        this.thirdObj = [];
        this.fourthObj = [];
        this.fifthObj = [];
        this.sixthObj = [];

      },
      select2To3: function(){

        for( let obj of this.secondObj.children ){
          if( obj.categoryId == this.second ){
            this.thirdObj = obj;
            // this.third = this.thirdObj[0].categoryId;
          }
        }

        this.handlerCategoryId( this.thirdObj );

        console.log( '********* third *********' );
        console.log( this.thirdObj );

        // 清楚后面select值
        this.fourthObj = [];
        this.fifthObj = [];
        this.sixthObj = [];

      },
      select3To4: function(){
        for( let obj of this.thirdObj.children ){
          if( obj.categoryId == this.third ){
            this.fourthObj = obj;
            // this.third = this.thirdObj[0].categoryId;
          }
        }

        this.handlerCategoryId( this.fourthObj );

        console.log( '********* fourth *********' );
        console.log( this.fourthObj );

        // 清楚后面select值
        this.fifthObj = [];
        this.sixthObj = [];

      },

      select4To5: function(){
        for( let obj of this.fourthObj.children ){
          if( obj.categoryId == this.fourth ){
            this.fifthObj = obj;
            // this.third = this.thirdObj[0].categoryId;
          }
        }

        this.handlerCategoryId( this.fifthObj );

        console.log( '********* fifth *********' );
        console.log( this.fifthObj );

        // 清楚后面select值
        this.sixthObj = [];

      },

      select5To6: function(){
        for( let obj of this.fifthObj.children ){
          if( obj.categoryId == this.fifth ){
            this.sixthObj = obj;
            // this.third = this.thirdObj[0].categoryId;
          }
        }

        this.handlerCategoryId( this.sixthObj );

        console.log( '********* fifth *********' );
        console.log( this.fifthObj );
      },


    },
    watch: {
     'categoryId' : function(_new, _old){
      this.$emit( 'categoryIdChange', _new );
     },
    }
  }
</script>



