<style lang="less" scoped>
    @import "./preview.less";
</style>

<template>
    <div class="preview" v-show="isShow">
        <div class="hd" v-if="title">{{title}}</div>
        <div class="bd">
            <nav class="imgNav">
                <a href="" :class="{active: index == cur}" v-for="(img, index) in imglist"
                   @click.prevent="cur=index">{{index + 1}}</a>
            </nav>
            <div class="imgShow" @click.prevent="handlerClosePreview">
                <img alt="" v-for="(img, index) in imglist" :src="img" :class="{active: index == cur}">
            </div>
        </div>
        <div class="ft">
            <a href="" class="closed" @click.prevent="handlerClosePreview"><span
                    class="glyphicon glyphicon-remove"></span></a>
            <a href="" :class="['btn left', {'none': cur == 0}]" @click.prevent="handlerBtnLeft"><span
                    class="glyphicon glyphicon-arrow-left"></span></a>
            <a href="" :class="['btn right', {'none': cur == imglist.length - 1}]" @click.prevent="handlerBtnRight"><span
                    class="glyphicon glyphicon-arrow-right"></span></a>
        </div>
    </div>

</template>

<script>
    export default {
        props: [
            'imglist',
            'title',
            'isShow',
            'static'
        ],
        data: function () {
            return {
                cur: 0
            }
        },

        created: function () {
        },
        ready: function () {
            this.init()
        },
        methods: {
            init: function () {

            },
            create: function () {

            },
            handlerClosePreview: function () {

                this.cur = 0;
                // this.isShow = false;
                this.$emit('closePreview');
            },
            handlerBtnLeft: function () {

                if (this.cur == 0) return;
                this.cur--;
            },
            handlerBtnRight: function () {
                if (this.cur == this.imglist.length - 1) return;
                this.cur++;

            },
            watch: {
                'isShow': function () {

                }
            }
        }
    }
</script>



