                  </select>
                </div>

                <div class="col-xs-6 col-sm-6">
                  <template v-for="model in buyerOnTry.modelConfig">
                    <p class="txtP c-999" v-if="model.id == buyerOnTry.onTryData.modelId">已选模特信息：{{model.modelHigh}}cm/{{model.modelWeight}}kg,三围{{model.modelBwh}}</p>
                  </template>

                </div>
              </div>

              <div class="row box">
                <div class="col-xs-2 col-sm-2 tr">
                 <label class="control-label">试穿尺码:</label>
                </div>
                <div class="col-xs-4 col-sm-4 box">
                  <select class="form-control"  v-model="buyerOnTry.onTryData.size">
                    <option value="">请选择</option>
                    <option v-for="size in skuInfo.size" :value="size">{{size}}</option>

                   </select>
                </div>
              </div>

              <div class="row box">
                <div class="col-xs-2 col-sm-2 tr">
                 <label class="control-label">试穿描述:</label>
                </div>
                <div class="col-xs-8 col-sm-8 box">
                  <textarea class="form-control" rows="3" placeholder="试穿描述" maxlength="140" v-model="buyerOnTry.onTryData.description"></textarea>
                  <span class="inputCount">{{buyerOnTry.onTryData.description ? buyerOnTry.onTryData.description.length : 0}}/140</span>
                </div>
              </div>

              <div class="row box ">
                <div class="col-xs-2 col-sm-2 tr">
                 <label class="control-label">大尺寸图:</label>
                </div>
                <div class="col-xs-10 col-sm-10 box">
                  <div class="uploadWrap firstUploadWrap">
                    <div class="upload-img">
                      <image-upload
                          name="imageUpload6"
                          width="154"
                          height="100"
                          maxlength="1"
                          auto="true"
                          cropper="true"
                          cropperminwidth="900"
                          cropperminheight="600"
                          cropperratio="1.5"
                          single="true"
                          preview="true"
                          again="true"
                          :result.sync="[buyerOnTry.onTryData.firstImg]"
                          @resultChange="handlerFirstImageChange"
                          @showImgChange="handlerPreview"
                          sizeType="6"
                          >
                      </image-upload>
                    </div>

                  </div>
                <p class="red">【1张】大图尺寸要求：最小宽度750，宽/高比=1.5，建议尺寸：900x600。</p>
                </div>

              </div>


              <div class="row box">
                <div class="col-xs-2 col-sm-2 tr">
                 <label class="control-label">小尺寸图:</label>
                </div>
                <div class="col-xs-10 col-sm-10 box">

                  <div class="uploadWrap">
                     <image-upload
                        name="imageUpload4"
                        width="100"
                        height="143"
                        maxlength="3"
                        auto="true"
                        cropper="true"
                        cropperminwidth="346"
                        cropperminheight="450"
                        cropperratio="0.768888"
                        multiple="true"
                        again="true"
                        download="true"
                        :result="buyerOnTry.onTryData.imgs"
                        @resultChange="handlerBuyerTryImageChange"
                        @showImgChange="handlerPreview"
                        sizeType="4"
                        drag="true">
                    </image-upload>
                  </div>

                  <div class="extend">
                    <p class="red">【3张】小图尺寸要求：最小宽度246，宽/高比=0.769，建议尺寸：346x450。
                      <template v-if="buyerOnTry.onTryData.imgs.length > 0">
                        <a href="javascript:;" class="download-link"  @click=handlerPreviewAllBuyer>预览</a> <a href="javascript:;" class="download-link" @click.prevent="downloadAllBuyer">下载全部详情图</a>
                      </template>
                    </p>
                  </div>
                </div>
              </div>


            </div>
          </div>

          <!-- 尺码效果 -->
          <div class="form-group clearfix">
            <div class="col-xs-2 col-sm-2 tr">
              <label class="control-label">尺码效果</label>
            </div>
            <div class="col-xs-10 col-sm-10">
              <div class="selectList">
                <ul>
                  <template v-for="(model, index) in buyerOnTry.modelConfig">
                      <li :class=" model.isCur ? 'current': '' " @click="handlerBuyerSize(index, $event)">{{model.nickName}}</li>
                  </template>
                </ul>
              </div>

              <div class="tab-content">
                <div class="table-wrapper">
                  <table class="table table-bordered">
                    <thead>
                      <tr>
                        <th>试穿人</th>
                        <th>身高/体重</th>
                        <th>三围</th>
                        <th>平时尺寸<span class="red">*</span></th>
                        <th>试穿尺码<span class="red">*</span></th>
                        <th>试穿效果<span class="red">*</span></th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(buyer, index) in buyerOnTry.onTryData.sizeEffect">
                        <td><span>{{buyer.nickName}}</span></td>
                        <td><span>{{buyer.heightWeight}}</span></td>
                        <td><span>{{buyer.bwh}}</span></td>
                        <td>
                          <select class="form-control"  v-model="buyer.commonSize">
                            <option value="">请选择</option>
                            <option v-for="list in buyer.sizeList" :value="list.value">{{list.name}}</option>
                           </select>
                        </td>
                        <td>
                          <select class="form-control"  v-model="buyer.trySize">
                            <option value="">请选择</option>
                            <option v-for="size in skuInfo.size" :value="size">{{size}}</option>
                           </select>
                        </td>
                        <td>
                          <select class="form-control"  v-model="buyer.effect">
                            <option value="">请选择</option>
                            <option v-for="effect in buyerOnTry.effectConfig" :value="effect">{{effect}}</option>
                           </select>
                        </td>
                      </tr>

                    </tbody>
                    <tfoot>

                    </tfoot>
                  </table>
                </div>
              </div>
            </div>
          </div>

          <!-- 买手亲测 -->
          <div class="form-group clearfix">
            <div class="col-xs-2 col-sm-2 tr">
              <label class="control-label">买手亲测</label>
            </div>
            <div class="col-xs-10 col-sm-10">

              <div class="upload-1"
                v-for="(buyer, index) in buyerExperience"
                v-dragging="{ item: buyer, list: buyerExperience, group: 'buyerExperience' }"
                :key=""
                >

                <div class="upload-img" >
                  <image-upload
                      name="imageUpload5"
                      width="143"
                      height="100"
                      maxlength="1"
                      auto="true"
                      cropper="true"
                      cropperminwidth="600"
                      cropperminheight="420"
                      cropperratio="1.428571"
                      single="true"
                      preview="true"
                      again="true"
                      :index="index"
                      :result='[buyer.image]'
                      @resultChange="handlerBuyerExperienceImageChange"
                      @showImgChange="handlerPreview"
                      sizeType="5"
                      >
                  </image-upload>
                </div>


                <div class="upload-con">
                  <div class="upload-box">
                    <input type="text" class="form-control" maxlength="10"  placeholder="试穿标题" v-model="buyer.title">
                    <span class="inputCount">{{buyer.title.length}}/10</span>
                  </div>
                  <div class="upload-box">
                    <textarea class="form-control" rows="3" maxlength="50" placeholder="试穿描述" v-model="buyer.description"></textarea>
                    <span class="inputCount">{{buyer.description.length}}/50</span>
                  </div>
                </div>

                <div class="upload-tool">
                  <a href="javascript:;" class="del" @click="handlerBuyerDel(index)"><span class="glyphicon glyphicon-remove"></span></a>
                </div>

              </div>

              <div class="row tr box">
                <div class="col-xs-12 col-sm-12">
                  <button type="button" class="btn btn-danger" @click="handlerBuyerAdd">添加</button>
                </div>
              </div>

              <div class="extend">
                <p>建议尺寸：图片尺寸要求，宽度最小400，宽/高比=1.428。建议尺寸600*420 </p>
              </div>
            </div>
          </div>

        </div>
      </div>


      <!-- 商品类目 -->
      <div class="editBox" id="category" v-if="propertyList.length > 0">
        <h3>商品类目属性<span class="red">（单击"选中"，再次点击"取消选择"）</span></h3>
        <div class="editBox-body">
          <!-- 产品说明 -->
          <div class="form-group clearfix">
            <!-- select -->
            <div class="col-xs-12 col-sm-12 prop-row" v-for="(list, index) in propertyList">
              <template v-if="list.multiSelect < 0">
                <div class="form-group">
                  <label class="col-sm-2 control-label tr"><span class="red" v-if="list.required == 1">*</span>{{list.propertyName}}</label>
                  <div class="col-sm-10">
                    <!--<select class="form-control" v-model="list.selected">-->
                      <!--<option value="">请选择</option>-->
                      <!--<option v-for="val in list.values" :value="[val]">{{val}}</option>-->
                    <!--</select>-->
                    <label class="label-radio" v-for="val in list.values">
                        <input type="radio" :value="[val]" v-model="list.selected"> {{val}}
                    </label>
                  </div>
                </div>
              </template>
              <template v-else>
                  <div class="form-group">
                      <label class="col-sm-2 control-label tr"><span class="red" v-if="list.required == 1">*</span>{{list.propertyName}}<span class="tips" v-if="list.maxInput > 1">(&le;{{list.maxInput}})</span></label>
                    <div class="col-sm-10">
                      <div class="provider-material" v-if="['材质','鞋面材质','内里材质','鞋底材质'].indexOf(list.propertyName) > -1 && goodsInfo.providerMaterial">{{ goodsInfo.providerMaterial }}</div>
                      <select-box
                        :result="list"
                        :index="index"
                        @resultChange="handlerPropertyListChange"
                      ></select-box>
                    </div>
                  </div>
              </template>
            </div>
          </div>

        </div>
      </div>

      <footer>
        <button class="btn btn-danger btn-lg" @click.prevent="handleSubmit" style="margin-right:20px"  v-if="!isPre">保存</button>
        <button class="btn btn-danger btn-lg" @click.prevent="handleSubmit(1)" v-if="goodsInfo.onNew != 1 && !isPre">保存并上新</button>
      </footer>
      <preview
        :imglist="showImgList"
        :isShow="isShow"
        :static="goodsInfo.staticDomain"
        @closePreview="changePreview"
        ></preview>


        <!-- 修改分类 -->
        <div id="editorCategory" class="modal fade" role="dialog">
          <div class="modal-dialog" style="width:950px;">
            <div class="modal-content">
              <div class="modal-header">
                <button type="button"
                        class="close"
                        data-dismiss="modal">
                  <span aria-hidden="true">&times;</span>
                  <span class="sr-only">Close</span>
                </button>
                <h4 class="modal-title">商品分类修改</h4>
              </div>
              <div class="modal-body">

                <div class="form-group clearfix">
                  <multiple-select
                    :result="category"
                    @categoryIdChange="handlerCategoryChange"
                  ></multiple-select>
                </div>

              </div>
              <div class="modal-footer">
                <button type="button"
                        class="btn btn-primary"
                        data-dismiss="modal"
                        @click="updateCategory">保存</button>
                <button type="button"
                        class="btn btn-default"
                        data-dismiss="modal">关闭</button>
              </div>
            </div>
          </div>
        </div>
    </section>

  </div>
</template>

<script>
  import App from './App.js';
  export default App;
</script>

<style lang="less">
  @import "common.less";
</style>

<style lang="less" scoped>
  @import "./app.less";
</style>



