<style lang="less" scoped>
  @import "../../../node_modules/cropperjs/dist/cropper.css";
  @import "./ImageUpload.less";
</style>

<template>
  <div class="image-upload-wrapper">
    <!-- 显示区域 -->
    <draggable :list="imageList" :options="{'disabled':!drag}">
      <div class="image-upload-item"
           :data-drag="drag"
           v-for="(item, index) in imageList"
           :key="item.src"
      >
        <div class="image-wrapper" :style="itemStyle">
          <div class="image-inner">
            <img class="preview-img"
                 :src="item.src"
                 :alt="item.name"
                 @click="handlerPreview(index)">
          </div>
        </div>
        <div class="image-description" v-show="this.description == 'true'">
          <div class="image-description-inner">
            <textarea maxlength="500" v-model="item.description"></textarea>
          </div>
        </div>

        <ul class="image-action">
          <li class="del" @click="removeImage(index)"><span class="glyphicon glyphicon-remove"></span></li>
          <li class="warn" :title="item.warnStr" v-show="item.warn == 0"><span class="glyphicon glyphicon-warning-sign"></span></li>
        </ul>
        <ul class="image-tool">
          <li class="preview" v-show="preview" @click="handlerPreview(index)">预览</li>
          <li v-show="cropper" @click="handlerCropper(index)">裁剪</li>
          <li class="upload" v-show="again" @click="handlerReUpload(index)">重新上传</li>
          <li class="download" v-show="download"><a target="_blank" :href="item.src" @click.prevent="downLoad(item.src)">下载图片</a></li>
        </ul>
      </div>
    </draggable>
    <!-- 默认区域 -->
    <div class="image-upload-item-label"
         v-show="parseInt(imageList.length) < parseInt(maxlength)">
      <label class="element-label" :style="itemStyle">
        <input type="file"
               name="file"
               class="element-invisible"
               multiple="multiple"
               accept="image/jpg, image/jpeg, image/png"
               @change="onFileChange">
        <p>
          <img class="img" src="https://s10.mogucdn.com/p1/170210/idid_ie4tqyjwgjtdozdgmqzdambqgqyde_88x75.png">
        </p>
        <p>
          {{this.label?this.label:'点击添加图片'}}
        </p>
      </label>
    </div>
    <button v-if="!single" type="button" class="btn btn-info" @click.prevent="reloadImage(imageList)">点此重新加载破图</button>
    <div class="image-cropper-wrapper" v-show="imageCropperActive">
      <img :id="'image-cropper-' + imageCropperId" :src="imageCropperSrc" style="max-width: 100%;">
    </div>
    <div class="image-cropper-opt" v-show="imageCropperActive">
      <button type="button" class="btn btn-info" @click.prevent="imageCropperDestroy">取消</button>
      <!--<button type="button" class="btn btn-primary" @click.prevent="imageCropperRotate">旋转</button>-->
      <button type="button" class="btn btn-primary" @click.prevent="imageCropperCancel">直接上传</button>
      <button type="button" class="btn btn-success" @click.prevent="imageCropperCrop">裁剪并上传</button>
    </div>
    <div class="image-cropper-mask" v-show="imageCropperActive"></div>
  </div>

</template>

<script>
  /**
   *  参数说明
   *  width/height    定义每个单元格的大小, 无单位（赞不支持）
   *  multiple        是否支持多图选择
   *  cropper         是否支持裁剪（暂未完善）
   *  maxlength       最大上传数量
   *  auto            是否自动上传(现在都是自动上传)
   *  description     是否支持描述信息
   *  result          值为all 则返回完整对象List / 否则只返回上传成功的服务端地址（暂未完善）
   */
  import Vue from 'vue'
  import Constant from '../../constants/apis/api.js'
  import $ from 'jquery'
  import Toast from './toast'
  import Cropper from 'cropperjs'
  import draggable from 'vuedraggable'

//  import VueDND from 'awe-dnd'
//  Vue.use(VueDND);

  // import VueDragAndDrop from 'vue-drag-and-drop'
  // Vue.use( VueDragAndDrop )

  export default {
    props: [
      'width',
      'height',
      'multiple',
      'cropper',
      'cropperratio',
      'cropperminwidth',
      'cropperminheight',
      'maxlength',
      'resulttype',
      'result',
      'auto',
      'description',
      'label',
      'single',
      'pathSrc',
      'preview',
      'again',
      'download',
      'index',
      'drag',
      'limit',
      'sizeType',
      'name'
    ],
    components: { draggable },
    data: function () {
      return {
        itemStyle: {
          width: this.width + 'px',
          height: this.height + 'px'
        },
        imageCropper: null,
        imageCropperId: Date.now(),
        imageCropperSrc: '',
        imageCropperActive: false,
        tempFileObject: null,
        imageList: [],
        resultList: [],
        warnArr: [],
        syncFlag: false,
        curIndex: -1,
        timer: null,
        isClickReload: false
      }
    },
    created: function () {
    },
    mounted: function () {
      this.init();
    },
    deactivated: function () {
    },
    methods: {
      init: function () {
        let $element = $(this.$el);
        let self = this;
        let $input = $element.find('input');
        if (this.multiple && this.multiple.toLowerCase() == 'false') {
          $input.removeAttr('multiple');
          this.maxlength = 1;
        } else {
          $input.attr('multiple', 'multiple');
          this.maxlength = this.maxlength || 999;
        }

        for (let item of this.result) {
          var ci = self.checkImg(item)
          this.imageList.push({
            src: item,
            pathSrc: item,
            warn: ci.value,
            warnStr:ci.str
          });
        }

      },

      checkImg:function (img) {
        if (!img) {return {};}
        var typereg = img.match(/_(\d*)x(\d*).(jpg|png|jpeg)$/);
        if (!typereg) {
          // return
          return {value:false,str:"图片格式异常，只支持jpg、png、jpeg格式"};
        }
        var width = typereg[1], height = typereg[2], imgType = this.sizeType;
        if (!width || !height) {
            // return ;
            return {value:false,str:'图片尺寸异常'};
        }
        if (imgType == 3) {
            // 详情图只要求宽度不小于640
            if (width < 640) {
                // return ;
                return {value:false,str:'图片宽度不能小于640'};
            }
        } else if (imgType == 1) {
            // 封面图限制修改
            if (width < 640) {
                // return ;
                return {value:false,str:'图片最小宽度640'};
            }
            if (height < 832) {
                // return ;
                return {value:false,str:'图片最小高度832'};
            }
            if (height / width > 1.6 || height / width < 1.3) {
                // return ;
                return {value:false,str:'图片比例必须满足1.3<高度/宽度<1.6'};
            }
        } else if (imgType == 4) {
            // 买手试穿 小图
            if (width < 246) {
                // return ;
                return {value:false,str:'图片宽度不能小于246'};
            }
            if (width/height < 0.76 || width/height > 0.78) {
                // return ;
                return {value:false,str:'图片“宽度/高度”需要在0.76~0.78之间'};
            }
        } else if (imgType == 5) {
            // 买手亲测图
            if (width < 400) {
                // return ;
                return {value:false,str:'图片宽度不能小于400'};
            }
            if (width/height < 1.42 || width/height > 1.44) {
                // return ;
                return {value:false,str:'图片“宽度/高度”需要在1.42~1.44之间'};
            }
        } else if (imgType == 6) {
            // 买手试穿 大图
            if (width < 750) {
                // return ;
                return {value:false,str:'图片宽度不能小于750'};
            }

            if (Math.round(width / 1.5) != height) {
                // return ;
                return {value:false,str:'图片“宽度/高度”需要等于1.5'};
            }
        }
        return {value:true};
      },

      handlerPreview: function (index) {
        this.$emit('showImgChange', [this.imageList[index].src]);
      },
      handlerReUpload: function (index) {
        this.curIndex = index;
        this.isClickReload = true;
        $(this.$el).find('.element-label').trigger('click');
      },
      onFileChange: function (e) {
        let files = e.target.files || e.dataTransfer.files;
        if (!files.length) {
          return;
        }
        let len = 1;
        if (this.maxlength == 1) {
          len = 1;
        } else {
          len = files.length > this.maxlength ? this.maxlength : files.length;
        }

        for (let i = 0; i < len; i++) {
          let tempObject = {};
          tempObject.lastModified = files[i].lastModified;
          tempObject.lastModifiedDate = files[i].lastModifiedDate;
          tempObject.name = files[i].name;
          tempObject.size = files[i].size;
          tempObject.type = files[i].type;
          tempObject.file = files[i];
          tempObject.id = this.__getuuid(10, 64) + new Date().getTime();
          // this.imageList.push( tempObject );
          this.tempFileObject = tempObject;
          this.createImagePreview(tempObject, len);
        }
      },
      downLoad: function (src) {
        let _url = '?url=' + JSON.stringify([src]);
        let url = Constant.DOWNLOAD_IMG + _url;
        window.open(url, '_blank');

      },
      createImagePreview: function (_object, imgSum) {
        let self = this;

        if (!this.isClickReload && self.imageList.length > self.maxlength - 1) {
          Toast.error({
            message: '超过传图最大个数',
            duration: 1500
          });
          return;
        }
        let reader = new FileReader();
        // 注：异步操作
        reader.onload = (e) => {
          _object.base64 = e.target.result;
          // 暂时只支持单个图片裁剪
          if (self.cropper && imgSum === 1) {
            self.imageCropperInit(_object.base64);
          } else {
            self.handleImageUpload(_object);
          }
        };
        reader.readAsDataURL(_object.file);
      },
      reloadImage (list) {
        // 解决上传成功后cdn同步延时问题
        this.imageList = [];
        setTimeout(() => {
          this.imageList = list;
        }, 300);
      },
      imageCropperInit (imageSrc) {
        let self = this;
        self.imageCropperSrc = imageSrc;
        self.imageCropperActive = true;
        setTimeout(function() {
          let image = document.querySelector('#image-cropper-' + self.imageCropperId);
          // 最大比例，最小比例，暂时取一个
          let ratio = self.cropperratio ? self.cropperratio.split(',') : [1];
          if (self.imageCropper) {
            self.imageCropper.destroy();
          }
          self.imageCropper = new Cropper(image, {
            aspectRatio: ratio[0],
            dragMode: 'move',
            autoCropArea: 0.985,
            viewMode: 1
          });
        }, 0);
      },
      handlerCropper (index) {
        this.curIndex = index;
        this.isClickReload = true;
        this.imageCropperInit(this.imageList[index].src);
      },
      imageCropperDestroy () {
        this.imageCropperActive = false;
        this.imageCropper.destroy();
        this.clearInput();
      },
      imageCropperRotate () {
        this.imageCropper.rotate(90);
      },
      imageCropperCancel () {
        this.handleImageUpload(this.tempFileObject);
        this.imageCropperActive = false;
        this.imageCropper.destroy();
      },
      imageCropperCrop() {
        let self = this;
        // 如果图片原始宽度或者高度小于裁剪尺寸，则设置最小裁剪尺寸，否则按比例裁剪，最大2倍
        let sizeObj = {
            fillColor: '#fff'
        };
        if (this.cropperminwidth >= this.imageCropper.element.naturalWidth || this.cropperminheight >= this.imageCropper.element.naturalHeight) {
          sizeObj.width = this.cropperminwidth;
          sizeObj.height = this.cropperminheight;
        }
        if (this.cropperminwidth * 2.5 < this.imageCropper.element.naturalWidth && this.cropperminheight * 2.5 < this.imageCropper.element.naturalHeight) {
          sizeObj.width = this.cropperminwidth * 2.5;
          sizeObj.height = this.cropperminheight * 2.5;
        }
        this.imageCropper.getCroppedCanvas(sizeObj).toBlob(function(blob) {
          let _object = {
            file: blob,
            id: self.__getuuid(10, 64) + new Date().getTime()
          };
          self.handleImageUpload(_object);
          self.imageCropperDestroy();
        }, 'image/jpeg');
      },
      // 清除上一次选择的值，防止传图失败后无法上传同一张图
      clearInput () {
        let $element = $(this.$el);
        let $input = $element.find('input');
        $input.val('');
      },
      handleImageUpload: function (_object) {
        let self = this;
        this.isClickReload = false;

        Toast.loading({
          message: '上传中…',
          duration: 0
        });
        self.upload(
          _object.file,
          function (responseData) {
            if (+responseData.status === 1) {
              Toast.error({
                message: responseData.msg || '图片上传失败，请重试！',
                duration: 1500
              });
              return false;
            }
            // success callback
            Toast.success({
              message: '上传成功',
              duration: 500
            });
            let _tmpObj = self.imageList;
            let _obj = {};
            if (_tmpObj.length > 0) {
              for (let i = 0; i < _tmpObj.length; i++) {

                if (_tmpObj[i].id == _object.id) {

                  self.imageList.splice(i, 1);

                  _obj.src = responseData.data[0];
                  _obj.pathSrc = responseData.path_data[0];
                  _obj.warn = responseData.validSize;
                  _obj.warnStr = "图片不符合规则";
                  self.warnArr[i] = responseData.validSize;
                } else {
                  _obj.src = responseData.data[0];
                  _obj.pathSrc = responseData.path_data[0];
                  _obj.warn = responseData.validSize;
                  _obj.warnStr = "图片不符合规则";
                  self.warnArr[i] = responseData.validSize;
                }
              }
            } else {
              _obj.src = responseData.data[0];
              _obj.pathSrc = responseData.path_data[0];
              _obj.warn = responseData.validSize;
              _obj.warnStr = "图片不符合规则";
              self.warnArr[0] = responseData.validSize;
            }

            if (self.curIndex > -1) {
              self.imageList.splice(self.curIndex, 1, _obj);
            } else {
              // self.imageList.push( _object );
              self.imageList.push(_obj);
            }

            // 图片不符合规则
            /*if( self.limit && responseData.validSize == 0 ){
             // alert( '图片不符合规则' );
             return
             }*/

            self.curIndex = -1;
            self.syncData();
            self.clearInput();
          }, function (responseData) {
            Toast.error({
              message: responseData.msg || '上传失败，请重试！',
              duration: 1500
            });
            self.clearInput();
            // error callback
          }, function (e) {
            // progress callback
            if (e.lengthComputable) {
              let percent = e.loaded / e.total * 100;
            }
          }
        );
      },
      removeImage: function (_index) {
        this.imageList.splice(_index, 1);
        this.syncData();
      },
      syncData: function () {
        this.syncFlag = true;
        if (this.resulttype == 'all') {
          this.result = this.imageList;
        } else {
          let tempArray = [],
            tempPathArray = [];
          for (let i = 0; i < this.imageList.length; i++) {
            tempArray.push(this.imageList[i].src);
            tempPathArray.push(this.imageList[i].pathSrc);
          }

          let _index = '';
          if (this.single) {
            _index = this.index
          }
          if (this.pathSrc) {
            this.$emit('resultChange', tempPathArray);
          } else {
            this.$emit('resultChange', tempArray, _index);
          }

        }
      },
      upload: function (_file, _successCallback, _errorCallback, _progressCallback) {
        let formData = new FormData();
        formData.append('file', _file);
        let uploadUrl = Constant.IMAGE_UPLOAD + '?sizeType=' + this.sizeType;
        let xhr = new XMLHttpRequest;
        xhr.open('POST', uploadUrl, true);
        xhr.onreadystatechange = function (e) {
          if (this.readyState == 4) {
            if (this.status == 200) {
              if (_successCallback && typeof _successCallback == 'function') {
                _successCallback(JSON.parse(this.response));
              }
            } else {
              if (_errorCallback && typeof _errorCallback == 'function') {
                _errorCallback(JSON.parse(this.response));
              }
            }
          }
        };
        xhr.ontimeout = function (e) {
          console.log('upload timeout');
          if (_errorCallback && typeof _errorCallback == 'function') {
            _errorCallback({msg: '上传超时，请重试！'});
          }
        };
        xhr.onerror = function (e) {
          console.log('upload error');
          if (_errorCallback && typeof _errorCallback == 'function') {
            _errorCallback({msg: '上传出错，请重试！'});
          }
        };
        xhr.upload.onprogress = function (e) {
          if (_progressCallback && typeof _progressCallback == 'function') {
            _progressCallback(e);
          }
        };
        xhr.send(formData);
      },
      // 获取 唯一id  len 长度 , radix 进制
      __getuuid: function (len, radix) {
        let chars = '0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz'.split('');
        let uuid = [], i;
        radix = radix || chars.length;
        if (len) {
          for (i = 0; i < len; i++) uuid[i] = chars[0 | Math.random() * radix];
        } else {
          let r;
          uuid[8] = uuid[13] = uuid[18] = uuid[23] = '-';
          uuid[14] = '4';
          for (i = 0; i < 36; i++) {
            if (!uuid[i]) {
              r = 0 | Math.random() * 16;
              uuid[i] = chars[(i == 19) ? (r & 0x3) | 0x8 : r];
            }
          }
        }
        return uuid.join('');
      }
    },
    watch: {
      'result': {
        handler: function (_newValue, _oldValue) {
          // syncFlag 用于保证组件赋值给父组件时，双向绑定不会反复触发
          // console.info('['+new Date().getTime()+']:::thisSync:::' , this.syncFlag )
          // console.info('['+new Date().getTime()+']:::newValue:::' , _newValue )
          // console.info('['+new Date().getTime()+']:::oldValue:::' , _oldValue )
          if (this.syncFlag) {
            this.syncFlag = false;
            return;
          }
          let tempArray = [];
          if (_newValue && _newValue.length) {
            for (let i = 0; i < _newValue.length; i++) {
              //
              if (_newValue[i] && typeof _newValue[i] == 'object') {
                if (!_newValue[i].base64) {
                  _newValue[i].base64 = _newValue[i].src;
                }

                tempArray.push(_newValue[i]);
                //
              } else if (typeof _newValue[i] == 'string') {
                var ci = this.checkImg(_newValue[i])
                tempArray.push({
                  src: _newValue[i],
                  pathSrc: _newValue[i],
                  base64: _newValue[i],
                  warn: ci.value,
                  warnStr:ci.str
                })
              }
            }
          }
          this.imageList = tempArray;
          // this.syncData();

        },
        deep: true
      },

      'imageList': function (newValue, oldValue) {
        if (this.drag) {
          this.syncData();
        }

      }

    }
  }
</script>



